.weather-icon {
	max-width: 10rem;
	max-height: 10rem;
}

.searchBar input {
	font-size: 2rem;
}

.searchBar button {
	font-size: 2rem;

	padding: 0.5rem;
}

.searchBar button:hover,
.searchBar button:focus {

}

header {
	background-color: #bed6ec;
}

.header-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	padding: 3rem 0;
}

.header-right {
	display: flex;
}

h1 {
	font-size: 4rem;
}

.searchBar {
	padding-left: 2rem;
}
.currentWeather-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.currentWeather {
	display: flex;
	justify-content: center;

	padding: 3rem 0;
}

.currentWeather-right {
	display: flex;
	flex-direction: column;
}

.description {
	text-transform: capitalize;
}
.forecast-week-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.forecast-week {
	display: flex;

	padding-top: 1rem;
}

.forecast-day {
	/* display: flex; */
	text-align: center;

	padding: 1rem;

	width: calc(100% / 7);
}
.forecast-hourly-container {
	display: flex;
	flex-direction: column;
	align-items: center;

	padding: 3rem 0;
	margin: 0 auto;

	max-width: 70%;
}

.forecast-hourly {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	grid-template-rows: 1fr;

	padding-top: 1rem;
}

.forecast-hour {
	padding: 0.5rem;
	text-align: center;
}
.LocationList {
	border: 1px solid black;
}
footer {
	background-color: #4b545d;
	position: absolute;
	bottom: 0;
	width: 100%;
}

.footer-container {
	padding: 2rem 0;
	color: white;
}
